var albums=[{id:'aventine',title:'Aventine',artist:'Agnes Obel',coverUrl:__WEBPACK_IMPORTED_MODULE_0__img__["b" /* CoverArt */].Aventine,bgUrl:'https://i.ytimg.com/vi/dw_Kk7M59SA/maxresdefault.jpg',imgCaption:'piasrecordings / YouTube',albumUrl:'https://open.spotify.com/embed/album/0T6V4pt9LwYgrYTEQrjISP',lyrics:{content:'\n        Oh how the hills were laughing<br />\n        Oh how the creeks they cried<br />\n        Oh how the grass would cheer on<br />\n        As we passed them by\n      ',song:'Pass Them By'},thoughts:'\n      <p>\n        Agnes Obel is an artist I just stumbled upon one day while listening to\n        a playlist on 8tracks (is that still a thing?). The first song I heard\n        from her was "The Curse" and I was immediately drawn in by the minimal,\n        classical style. Her music quickly became my go-to music for studying\n        or just relaxing and having a cup of tea.\n      </p>\n      <p>\n        "Pass Them By" is my favourite song on <em>Aventine</em>. I love the subtle\n        acoustic guitar in it and there\u2019s something about the lyrics (see above)\n        that I find very satisfying. Sometimes I imagine myself walking through\n        a veldt as the wind sweeps over the long blades of wild grass, they\n        rustle in the breeze, cheering me as I pass them by.\n      </p>\n      <p>\n        I also like the ambiguity of Agnes Obel\u2019s lyrics. She mumbles the words\n        in most of her songs which might be a turn-off for some people, but I\n        sort of enjoy it because it has led me to listen more attentively and\n        sometimes I get to imagine my own lyrics when I can\u2019t quite hear what\n        she\u2019s singing.\n      </p>\n    ',colors:{primary:'#CD4103',secondary:'#2A0306',text:'#ffffff'}},{id:'come-over-when-youre-sober',title:'Come Over When You\'re Sober, Pt. 1',artist:'Lil Peep',bgUrl:'http://the-flow.ru/uploads/images/resize/830x0/adaptiveResize/05/87/31/94/29/8ebfd9040cd7.png',imgCaption:'Eric T. White / Paper Magazine',coverUrl:'http://images.genius.com/5ebed3bf6f5790bdceb1130ca93fbd1d.939x939x1.jpg',albumUrl:'https://open.spotify.com/embed/album/33TJ52PuwenGmysJrwMGCe',thoughts:'\n      <p>\n        <em>Come Over When You\u2019re Sober, Pt. 1</em> is Lil Peep\u2019s only official\n        album, which he released just a few months before his death in\n        October 2017. Gaining a large following on SoundCloud, Lil Peep was\n        on the verge of making it into the mainstream. His songs were catchy\n        and but also dark in theme, which I found to be very cathartic. His\n        songs empathize with your sadness, but they can heal your heart and\n        change your mood in just seconds.\n      </p>\n      <p>\n        Lil Peep got most of his inspiration from rap and emo music. He\n        transformed those genres to create his unique sound. Being just\n        21 years old and having already built up a respectful following,\n        he would have certainly been an impactful artist in the music industry\n        in years to come had he not died.\n      </p>\n    ',lyrics:{content:'\n        I know that you want me, you know that I want you<br/>\n        The memories haunt me, I know that they haunt you too<br/>\n        But it\'s alright, you\'ll be fine\n      ',song:'The Brightside'},colors:{primary:'#FA2E45',secondary:'#FFC4D3',text:'#061C4B'}},{id:'how-i-learned',title:'How I Learned To Stop Giving A Shit And Love Mindless Self Indulgence',artist:'Mindless Self Indulgence',imgCaption:'Mindless Self Indulgence / altpress.com',coverUrl:'https://pp.userapi.com/c412328/v412328889/3bb3/iPMEe1HgzqA.jpg',bgUrl:'https://www.altpress.com/images/made/chrootimages/uploads/feature_header_images/mindlessselfindulgence620x400_633_461_70_s_c1.jpg',albumUrl:'https://open.spotify.com/embed/album/6LaVH1njSyg2fzS1MucTg7',thoughts:'\n      <p>\n        Mindless Self Indulgence thrive off their obscene lyrics and\n        upbeat electronic punk music. Their name is probably the most\n        accurate band name I know of. Their lyrics are consistently both\n        mindless and self-indulgent.\n      </p>\n      <p>\n        <em>How I Learned...</em> is the culmination of Mindless Self\n        Indulgence\u2019s work throughout their long career as a band. I like\n        MSI because their lyrics are simple, dark, and hilarious.\n        "It Gets Worse" bluntly tells you that your life is going to get\n        worse and you\u2019re going to die alone. But then "Ala Mode" is just\n        tells you that no matter how bad life gets, ice cream makes\n        everything better. So maybe it balances itself out. There\u2019s a lot\n        of randomness to the themes on this album, but one thing in common\n        about all the songs is their depressive, dark humour. Sometimes you\n        need a good punch in the face. This album can provide that.\n      </p>\n    ',lyrics:{content:'\n        You\'re a failure? Ice cream will fix it!<br/>\n        Everyone hates you? Ice cream will fix it!<br/>\n        Everyone hates you!<br/>\n        I want sprinkles on that shit\n      ',song:'Ala Mode'},colors:{primary:'#5FA9CE',secondary:'#8B2C6D',text:'#E0E0DF'}},{id:'imaginaerum',title:'Imaginaerum',artist:'Nightwish',coverUrl:__WEBPACK_IMPORTED_MODULE_0__img__["b" /* CoverArt */].Imaginaerum,bgUrl:__WEBPACK_IMPORTED_MODULE_0__img__["a" /* BackgroundImages */].Nightwish,imgCaption:'Nightwish / nightwish.com',albumUrl:'https://open.spotify.com/embed/album/1yssEiclrpPwxfzpMyHaHb',thoughts:'\n      <p>\n        <em>Imaginaerum</em> is probably the most well-rounded album from Nightwish.\n        It\u2019s the album that got me into the band and into symphonic metal in\n        general. At first, this was the only album from Nightwish I would\n        listen to, but after some time I gradually developed a taste for the\n        rest of their music as well, going back one album at a time.\n      </p>\n      <p>\n        As the band became more successful, they\u2019ve been able to afford to\n        produce better quality recordings overall. Producing music at the\n        scale and complexity that Nightwish does on this album \u2014 with a full\n        orchestra and hundreds of tracks \u2014 is rather expensive when compared\n        to most bands, who typically just need a handful of people to produce\n        a quality album. I think having means to properly produce the\n        extravagant sounds they intended has played a large role in making\n        <em>Imaginaerum</em> such a great album. Also in the context of <em>Imaginaerum</em>,\n        it\u2019s easier to listen to their earlier music and imagine a more\n        complete sound. For me, this album improves Nightwish\u2019s previous\n        works \u2014 it actually makes their older music better in its own\n        right \u2014  which makes this album an essential chapter in the band\u2019s\n        career.\n      </p>\n      <p>\n        The reasons why I enjoy this album/band so much is because they build\n        such an immersive world, and let is as the listener indulge in\n        escapism and lament our abandonment of childhood. The music and\n        lyrics build a complex narrative with both lyrical and musical\n        callbacks to earlier songs across their discography. Often the songs\n        take inspiration from classic fairytales, such as Alice\u2019s\n        <em>Adventures in Wonderland</em> and <em>Lord of the Rings</em>, ancient mythology,\n        and works of classic poets such as Edgar Allan Poe and Walt Whitman.\n        If you like to dive down the rabbit hole analyzing music and lyrics,\n        <em>Imaginaerum</em> and Nightwish will keep you busy for a good while.\n      </p>\n    ',lyrics:{content:'\n        Paper is dead without words<br />\n        Ink idle without a poem<br />\n        All the world dead without stories<br />\n        Without love and disarming beauty\n      ',song:'Song of Myself'},colors:{primary:'#D6D6D6',secondary:'#29455A',text:'#F2F3F8'}},{id:'into-your-lungs',title:'Into Your Lungs (and around in your heart and on through your blood)',artist:'Hey Rosetta!',imgCaption:'Hey Rosetta! / heyrosetta.com',bgUrl:'http://www.heyrosetta.com/images/open-graph.jpg',coverUrl:__WEBPACK_IMPORTED_MODULE_0__img__["b" /* CoverArt */].IntoYourLungs,albumUrl:'https://open.spotify.com/embed/album/3HX991kFppdRlBvKAmejt4',thoughts:'\n      <p>\n        Into Your Lungs is the oldest album on this list and one that is\n        responsible for shaping my current music tastes. Hey Rosetta! is\n        without question my favourite band from Atlantic Canada. I remember\n        first hearing New Goodbye back in 2008 and falling in love with Hey\n        Rosetta!\u2019s music. Even after a full decade of listening to this\n        album I\u2019m still not bored of it.\n      </p>\n      <p>\n        <em>Into Your Lungs</em> is the band\u2019s first album and I think it has\n        remained their best over the years. With each subsequent releases,\n        their music style has shifted from indie rock to a more synthetic\n        sound and in doing so I think they lost some of what drew me to\n        them in the first place. Into Your Lungs stands out to me because\n        it creates a complex soundscape, making heavy use of violin, cello,\n        and mandolin, in addition to more common instruments like guitar\n        and piano.\n      </p>\n    ',lyrics:{content:'\n        So we\'re taking the boat tonight<br/>\n        We\'re taking our aging lives<br/>\n        And we\'re waving a new goodbye<br/>\n        Our arms open wide\n      ',song:'New Goodbye'},colors:{primary:'#113657',secondary:'#EDEDED',text:'#181816'}},{id:'mothership',title:'Mothership',artist:'Dance Gavin Dance',imgCaption:'Dance Gavin Dance / brokeassstuart.com',bgUrl:'http://brokeassstuart.com/wp-content/pictsnShit/2017/02/14141995_1160654840657313_2221211444340838659_n.jpg',coverUrl:__WEBPACK_IMPORTED_MODULE_0__img__["b" /* CoverArt */].Mothership,albumUrl:'https://open.spotify.com/embed/album/1FSVYIieMIpJCWjucTjrao',thoughts:'\n      <p>\n        Dance Gavin Dance does a good job at not taking themselves too\n        seriously, while also being serious about making good music. Even\n        though their writing style is fairly superficial and thrives on\n        clich\xE9s, the band uses that style to present a light-hearted\n        perspective on heavier topics such as substance abuse, love and\n        heartbreak, and self-reflection on <em>Mothership</em>.\n      </p>\n      <p>\n        Where many artists I listen to talk about serious topics in a\n        serious tone. Dance Gavin Dance reminds me that I shouldn\u2019t take\n        things too seriously even if they\u2019re serious things. Their music\n        frames life\u2019s problems in a not so serious perspective.\n      </p>\n      <p>\n        I think Dance Gavin Dance\u2019s style is able to work so well because\n        they pair their unique lyricism with music that fuses elements\n        of pop and metal into a sound that is both catchy and gritty. They\n        hide some of their more questionable lyrics within dirty vocals.\n        Unintelligible at first, and even more insensible when you look\n        them up. Makes sense, no?\n      </p>\n    ',lyrics:{content:'\n        Gimme that canvas let me paint some shit<br />\n        Pass me some poison let me take a hit<br />\n        I\'m just embarrassed and comfortably numb<br />\n        But failure is painful and lying is fun\n      ',song:'Chucky vs. The Giant Tortoise'},colors:{primary:'#FEDF21',secondary:'#4A9B9B',text:'#fff'}},{id:'run-the-jewels-3',title:'Run The Jewels 3',artist:'Run The Jewels',imgCaption:'Run The Jewels / Wikipedia',coverUrl:'https://upload.wikimedia.org/wikipedia/en/9/96/RunTheJewels3.jpg',bgUrl:'https://www.fuse.tv/image/51b62280cfa61e8077000029/816/545/run-the-jewels-killer-mike-elp.jpg',albumUrl:'https://open.spotify.com/embed/album/3v2GjFB9V5kHgrOCXn3sI9',thoughts:'\n      <p>\n        Killer Mike and El-P, together known as Run The Jewels (RTJ),\n        have been pumping out hard album after hard album over the past\n        few years and it just keeps getting better with each release.\n      </p>\n      <p>Simply put: I like Run The Jewels because it\u2019s hype AF.</p>\n      <p>\n        Run The Jewels drops hard bars on top of hard beats on every\n        track. They\u2019re relentless. Lyrically, RTJ\u2019s main focus is\n        unforgiving self-aggrandizement with the some misanthropic\n        commentary sprinkled on top. They paint vivid imagery with\n        their lyrics that is unmatched in today\u2019s rap scene.\n      </p>\n      <p>\n        I arbitrarily picked their most recent album for this list, but\n        <em>RTJ1</em> and <em>RTJ2</em> are both on equal footing with\n        <em>RTJ3</em>, and my thoughts here apply to all three albums equally.\n        I can\u2019t wait for <em>RTJ4</em>.\n      </p>\n    ',lyrics:{content:'\n        Doctors of death<br/>\n        Curing our patients of breath<br/>\n        We are the pain you can trust<br/>\n        Crooked at work<br/>\n        Cookin\' up curses and slurs\n      ',song:'Legend Has It'},colors:{primary:'#D2A402',secondary:'#101220',text:'#00A1B4'}},{id:'skin-and-earth',title:'Skin & Earth',artist:'Lights',coverUrl:__WEBPACK_IMPORTED_MODULE_0__img__["b" /* CoverArt */].SkinAndEarth,imgCaption:'Matt Barnes / cbc.ca',bgUrl:'https://i.cbc.ca/1.4296690.1505836343!/fileImage/httpImage/image.jpg_gen/derivatives/16x9_940/lights.jpg',albumUrl:'https://open.spotify.com/embed/album/6s7WpIWoAG0LiBzQuuPh9o',thoughts:'\n      <p>\n        Lights is an interesting artist. I love how she not only makes\n        music, but also likes to create visual art and write and illustrate\n        her own comics and graphic novels. With her album Skin & Earth,\n        she released a comic series that gives additional context and\n        meaning to the songs.\n      </p>\n      <p>\n        Lights was the first artist to really get me into electronic\n        music. I like Lights because she puts a unique twist on electronic\n        music. Before hearing Lights for the first time I didn\u2019t think\n        electronic music had anything more to offer beyond bass drops\n        and dissonant square synths. Everything else I heard from the\n        genre didn\u2019t do much for me.\n      </p>\n      <p>\n        I first got into Lights with the release of her album <em>Siberia</em>.\n        Her next album after that, <em>Little Machines</em>, didn\u2019t resonate with\n        me as much though. <em>Skin And Earth</em> got me back into her music again\n        though. I don\u2019t have too much to say about this album specifically,\n        other than that I like it the most out of all albums from Lights.\n        She really did an amazing job with all the tracks on <em>Skin And Earth</em>.\n      </p>\n    ',lyrics:{content:'\n        I was a knife in a gun fight <br />\n        And I fought so madly<br />\n        You were a wolf in the daylight<br />\n        And you almost had me\n      ',song:'Almost Had Me'},colors:{primary:'#E85455',secondary:'#ECC6B3',text:'#4A4A4A'}},{id:'sturm-und-drang',title:'VII: Sturm Und Drang',artist:'Lamb of God',imgCaption:'Lamb of God / etalinjection.com',bgUrl:'http://www.metalinjection.net/wp-content/uploads/2014/12/Lamb-Of-God-new-album-2015.jpg',coverUrl:__WEBPACK_IMPORTED_MODULE_0__img__["b" /* CoverArt */].SturmUndDrang,albumUrl:'https://open.spotify.com/embed/album/01Lg3FKNxZ0We36Exz8Q9V',thoughts:'\n      <p>\n        Lamb of God are easily the heaviest band on this list. Even the\n        lead singer, Randy Blythe, once described their music as\n        \u201Cunlistenable\u201D, but they\u2019re still one of my favourite bands. I feel\n        a strong connection with Randy\u2019s writing style; I think his lyrics\n        often mirror the style and themes of my own writing so his words\n        resonate with me on a very intimate level.\n      </p>\n      <p>\n        <em>VII: Sturm Und Dran</em> is the pinnacle of Lamb of God\u2019s discography.\n        The album is filled with bangers and gives clever critiques on\n        modern day topics such as mainstream media and social media with\n        the songs "Engage The Fear Machine" and "Delusion Pandemic". "Overlord"\n        stands apart from the rest of Lamb of God\u2019s entire career as it is\n        the first time we hear clean vocals from Randy Blythe.\n      </p>\n      <p>\n        The song "512" is one of the highlights of the album, recounting the\n        time Randy spent in prison in Prague, Czech Republic while on trial\n        for his involvement in the death of a fan at one of their shows a\n        few years prior. Eventually he was found not guilty and released,\n        but he was certainly shaken by the experience and the lyrics in\n        this song reveal the internal struggle he went through, the guilt\n        and remorse he felt. The band produced a documentary,\n        <em>As The Palaces Burn</em>, which tells more details about the incident.\n      </p>\n    ',lyrics:{content:'\n        The time is slipping by no peace in sight<br />\n        But the teeth of time still hold their bite\n      ',song:'512'},colors:{primary:'#815732',secondary:'#E2C69A',text:'#575444'}},{id:'thats-the-spirit',title:'That\'s the Spirit',artist:'Bring Me The Horizon',imgCaption:'Bring Me The Horizon / Epitaph Records',bgUrl:'http://epitaph.com/media/artists/BringMeTheHorizon_MegaImage_D9gUvdy.jpg.1200x630_q90_crop.jpg',coverUrl:__WEBPACK_IMPORTED_MODULE_0__img__["b" /* CoverArt */].ThatsTheSpirit,albumUrl:'https://open.spotify.com/embed/album/7FqHuAvmREiIwVXVpZ9ooP',thoughts:'\n      <p>\n        Bring Me The Horizon has switched up their style quite a bit in\n        recent years. The band\u2019s earlier music is some of the heaviest in\n        the metal genre, but with their two most recent albums, <em>Sempiternal</em>\n        and <em>That\u2019s The Spirit</em>, they\u2019ve gone in a more mainstream direction.\n        However, some elements of their music are rooted in their previous\n        style, such as lyrical themes and influences from electronic music.\n      </p>\n      <p>\n        I like <em>That\u2019s The Spirit</em> because it shows that the band is\n        comfortable exploring new directions and evolving their sound over\n        time. It\u2019s certainly the most approachable album in the band\u2019s\n        discography and it shows off the vocal of their lead singer,\n        Ollie Sykes. I think if you\u2019re going to like anything from Bring Me\n        The Horizon, <em>That\u2019s The Spirit</em> is the album to start with.\n        However, the opening and closing tracks are the weakest on the\n        album in my opinion. Aside from that, I like every track on\n        this album to a reasonable degree.\n      </p>\n    ',lyrics:{content:'\n        Who will fix me now?<br/>\n        Dive in when I\'m down?<br/>\n        Save me from myself<br/>\n        Don\'t let me drown\n      ',song:'Drown'},colors:{primary:'#ffffff',secondary:'#000000',text:'#ffffff'}},{id:'trouble-will-find-me',title:'Trouble Will Find Me',artist:'The National',imgCaption:'The National / blog.ticketmaster.co.uk',bgUrl:'https://blog.ticketmaster.co.uk/wp-content/uploads/2013/11/the-nationallead-1.jpg',coverUrl:__WEBPACK_IMPORTED_MODULE_0__img__["b" /* CoverArt */].TroubleWillFindMe,albumUrl:'https://open.spotify.com/embed/album/2JhR4tjuc3MIKa8v2JaKze',thoughts:'\n      <p>\n        The National have a very hypnotic sound to their music that evokes a\n        melancholy deep within. <em>Trouble Will Find Me</em> is the first album I\n        heard from the band and still remains my favourite. Although I\u2019ve\n        explored some of the band\u2019s older music, nothing else from them has\n        had such a lasting impression on me as this album.\n      </p>\n      <p>\n        Lyrics on <em>Trouble Will Find Me</em> alternate between specifics and\n        abstracts \u2014 between story and emotion. I like how the album\n        gives names to characters in the songs. The characters and\n        their stories breathe life into the music, grounding the songs in\n        reality, which is a stark contrast to the abstract lyrics that\n        surround them. I think this really pays off when Jennifer, who\n        is first mentioned in "Fireproof", reappears in the song\n        "This Is The Last Time" as Jenny. Knowing that these songs are\n        about the same person helps build a more cohesive character\n        and narrative. It suggests looking for other, less explicit,\n        connections between the songs that wouldn\u2019t be there otherwise.\n      </p>\n    ',lyrics:{content:'\n      Oh, when I lift you up you feel <br />\n      Like a hundred times yourself <br />\n      I wish everybody knew <br />\n      What\'s so great about you\n      ',song:'This Is The Last Time'},colors:{primary:'#555555',secondary:'#eeeeee',text:'#222222'}},{id:'two-parts-viper',title:'Two Parts Viper',artist:'`68',imgCaption:'`68 / metalnexus.net',bgUrl:'http://metalnexus.net/wp-content/uploads/2017/08/1458634_207950419388109_514796493_n.jpg',coverUrl:__WEBPACK_IMPORTED_MODULE_0__img__["b" /* CoverArt */].TwoPartsViper,albumUrl:'https://open.spotify.com/embed/album/5fLNoR3OoIbiNhYlkqxcDD',lyrics:{content:'\n        I could have been anyone from anywhere <br />\n        But I chose to be me from right here\n      ',song:'Whether Terrified or Unafraid'},thoughts:'\n      <p>\n        \u201968 just rocks out hard and holds nothing back. I first\n        discovered this rock duo shortly before they released their\n        first album (<em>In Humour and Sadness</em>) and I\u2019ve been a fan of their\n        stripped-down punk rock ever since.\n      </p>\n      <p>\n        With bands that come out strong on their first album I\u2019m\n        always nervous about their follow up album, but <em>Two Parts Viper</em>\n        sure delivered on my expectations as the band\u2019s second album.\n        It built on what worked from <em>In Humour And Sadness</em> and explored\n        a few new directions, one of which included some spoken-word\n        parts on "No Apologies" that reminded me of one of my favourite\n        poets, Allen Ginsberg.\n      </p>\n      <p>\n        \u201968 also put on incredible live performances with arrangements\n        that differ from the recordings. I\u2019ll often watch videos of their\n        concerts and session recordings because they always play variants\n        of their songs that add new layers of intrigue.\n      </p>\n    ',colors:{primary:'#E2EC4B',secondary:'#F14772',text:'#fff'}}];/* harmony default export */ __webpack_exports__["a"] = (albums);

